import shareicon from 'assets/shareicon.png'
import {api} from 'common/app'

let shareConfig = (url,data) => {
  var share_img =
    "http://wechat.crnonline.cn/readbook/html/static/media/" +
    shareicon.split("/")[shareicon.split("/").length - 1];
  var share_title = "";
  var share_content = "512";
  window.wx.onMenuShareAppMessage({
    title: share_title, // 
    desc: share_content, // 
    link: url, // 
    imgUrl: share_img, // 
    type: "link", // ,musicvideolinklink
    dataUrl: "", // typemusicvideo
    success: function() {
      // 
      if (data) {
          api.SharePost(data.type,data.id).then(res=>{
              console.log(res);
              
          },err=>{
              console.log(err);
              
          })
      }
    },
    cancel: function() {
      // 
    }
  });

  window.wx.onMenuShareTimeline({
    title: share_title, // 
    desc: share_content, // 
    link: url, // 
    imgUrl: share_img, // 
    type: "link", // ,musicvideolinklink
    dataUrl: "", // typemusicvideo
    success: function() {
      // 
      if (data) {
            api.SharePost(data.type,data.id).then(res=>{
            console.log(res);
            
            },err=>{
                console.log(err);
                
            })
        }
    },
    cancel: function() {
      // 
    }
  });
};

export default shareConfig



